.base {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    padding: 20px;
    font-size: smaller;
}

.base ul {
    display: flex;
    padding: 0;
    margin: 0;
    justify-content: center;
    align-items: center;
}

.base ul li img {
    position: absolute;
    transform: translateY(-50%);
    top: 50%;
    margin-left: 10px;
}

.base ul li {
    margin: 0 15px;
}

.base ul li, 
.base ul li a {
    color: var(--ui-color-subtle);
}

@media(max-width: 768px) {

    .base ul {
        flex-direction: column;
    }
    
    .base ul li img {
        position: relative;
        top: 0;
        transform: inherit;
        display: block;
        margin: 10px auto 0 auto;
    }
    
    .base ul li {
        margin: 5px;
        list-style: none;
    }
    
    .base {
        position: relative;
        padding: 0 20px;
    }

}